.gallery {

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery img {
  transition: transform 0.3s ease-in-out;
  flex:29%;
height: auto;
width: 35%;
  margin: 20px;
}
.gallery img:hover{
  transition: transform 0.3s ease-in-out;
  transform: scale(1.1);
}

.thumbnail {
  cursor: pointer;
  max-width: 100%;
}

.modal {
  position: fixed;
    transform: scale(1);
  align-items: center;
  background-color: rgba(0, 0, 0, 0.944);
  backdrop-filter: blur(10px); /* This creates a blurred background effect */

  height: 99%;
  justify-content: center;
  left: 0;
  padding: 1px;
  position: fixed;
  top: 0;
  transition: opacity ease-in-out 0.3s;
  width: 100%;
  z-index: 9999;
}

.modal-content {
  height: 100%;
  width: 100%;
  display: flex;

  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: opacity ease-in-out 0.3s;
}

.modal-content img {
 
}

.fotomodal {
  min-height: 99%;
  min-width: 99%;
  display: flex;
  align-items: center;
  /* Vertically center the content */
  justify-content: center;
  background-color: none;
  transition: transform 0.8s ease-in-out, opacity 0.9s ease-in-out;
  opacity: 1;
  /* Add this to set initial opacity to 1 */
}

/* Add this to your CSS file */
.fotomodal img {

  max-width:50%;


}
.Fot{
  display: fixed;
  z-index: 100000;
  transition: transform 0.4s ease-in-out,
}
.Fot:hover {
  transform: scale(1.2);
}

.modal-description {
  color: white;
  font-size: 18px;
  margin-top: 20px;
  text-align: center;
  width: 100%;
}

.modal-controls {


  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  transform: translateX(-50%);
  min-width: 100%;
  max-width: 99%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  box-sizing: border-box;
  z-index: 100;
}

.arrow {


  font-size: 1em;
  cursor: pointer;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

  background-color: rgba(0, 0, 0, 0);
  border: none;
  height: 100%;
  width: 10%;
  color: white;
  transition: transform 0.25s ease;
}

.arrow:hover {
  transform: scale(1.9);
}

.arrow:focus {
  transform: scale(1.1);
}

.arrow.prev {
  margin-right: 20%;
}

.arrow.next {
  margin-left: 20%;
}

.modal-close {


  background-color: rgba(0, 0, 0, 0.296);
  cursor: pointer;
  color: rgb(255, 255, 255);
  border: none;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 2em;
  position: absolute;
  top: 0;
  left: 50%;
  height: 5%;
  width: 50%;
  transform: translateX(-50%);
  z-index: 102;
}

@media screen and (max-width: 960px) {
  .modal-content {
    max-height: 90%;
  }

  .modal-content img {
    max-height: calc(100vh - 220px);
  }

  .modal-description {
    font-size: 16px;
  }
}

@media screen and (max-width: 600px) {
  .modal-controls {
    flex-direction: column;
  }

  .arrow {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .modal-close {
    right: 10px;
    top: 10px;
  }
}

.arrow:hover~.fotomodal {
  background-color: red;
}



.fotomodal.prev-or-next-clicked {
  transform: scale(1.2);
  opacity: 0;
  /* Add this to fade out the element */
}