.fullscreen-mode .controls {
  position: absolute;
  display: flex;
  font-size: 24px;
  padding: 1px;
  left: 0;
  opacity: 0;
  right: 0;
  bottom: 0;
}
.fullscreen-mode .controls:hover {
  position: absolute;
  display: flex;
  font-size: 24px;
  padding: 1px;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
}

.controls {
  margin: 5px 0 15px 0px;
  border-radius: 10px;
  max-height: 60px;
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
position:relative;
  width: 100%;
  height: auto; /* Встановлення висоти панелі керування */

  padding: 0 2px;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-family: Helvetica, Arial, sans-serif;
  transition: all .3s ease-in-out;
}
.controls button {
  background-color: transparent;
  border: none;
  padding: 8px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.controls button:hover {
  background-color: rgba(255, 255, 255, 0.08);
  transition: all 0.2s ease-in-out;
}
.controls button:focus {
  outline: none;
}
.controls input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  color:rgb(8, 0, 255);
  height: 5px;
  background-color: #ff0000;
  border-radius: 2px;
  margin: 0 8px;
  outline: none;
}
.controls input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #fff;
  border: none;
  margin-top: -4px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);
}
.controls input[type="range"]::-webkit-slider-runnable-track {
  background-color: #e600ff;
  border-radius: 2px;
}
.controls input[type="range"]:focus::-webkit-slider-thumb {
  background-color: #fff;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);
}
.bat-vol{
  max-width: 10%;
}
.bat-rate{
  max-width: 10%;
}


.video-wrapper.theater-mode {

  max-width: 70%;

}