  margin-right: 15px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.app.stthree .logo h1 {
  margin: 0;
}


.app.stthree .header-menu {
  display: flex;
}

.app.stthree .header span {
  cursor: pointer;
