}

.app.stthree a:hover {
  cursor: pointer;
  color: blue !important;

}

.app.stthree p:hover {
  color: blue !important;

}


.app.stthree span:hover {
  color: blue !important;

}

.app.stthree button:hover {
  color: blue !important;

}

.app.stthree h1:hover {
  color: blue !important;

}

.app.stthree li:hover {
  color: blue !important;

}





.app.stthree .wrapper {
  width: 0px;
  height: 0px;
  display: flex;
}

.app.stthree .left-side {
  flex-basis: 0;
  border-right: 0px solid var(--border-color);
  animation: border-animation 4s infinite;

  padding: 0;
  flex-shrink: 0;
}

.app.stthree .main-header {
  display: flex;
  align-items: center;
  border-bottom: 0px solid var(--border-color);
  height: auto;
  flex-shrink: 0;
}

.app.stthree .main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app.stthree .menu-link-main {
  text-decoration: none;
  color: var(--theme-color);
  padding: 0 30px;
}

@media screen and (max-width: 415px) {
  .adobe-product .menu {
    display: none;
  }
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-bg);
  border-radius: 10px;
}


.app.stthree .videolist {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
}


@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  25% {
    background-position: 100% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  75% {
    background-position: 0% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}



body {
  animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
  background-position: calc(var(--start) - 20%) 50%;
  animation-direction: alternate;
  animation-duration: 5s;
}




/* Определяем классы для анимации */
.app.stthree .slide-in {
  animation: slide-in 0.5s ease-out forwards;
}

.app.stthree .slide-out {
  animation: slide-out 0.5s ease-out forwards;
}

@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slide-out {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}



