@font-face {
  font-family: "kiena";
  src: url("./kiena.ttf") format("truetype");
}


:root .app.stone {
  font-size: 18pt;
  --theme-bg-color: rgba(16 18 27 / 40%);
  --border-color: rgba(113 119 144 / 25%);
  --theme-color: #ce162b;
  --inactive-color: rgb(113 119 144 / 78%);
  --body-font: "Poppins", sans-serif;
  --hover-menu-bg: rgba(12 15 25 / 30%);
  --content-title-color: #999ba5;
  --content-bg: rgba(39, 39, 41, 0.13);
  --button-inactive: rgb(249 250 251 / 55%);
  --dropdown-bg: #21242d;
  --dropdown-hover: rgb(42 46 60);
  --popup-bg: rgb(22 25 37);
  --search-bg: #14162b;
  --overlay-bg: rgba(36, 39, 59, 0.3);
  --scrollbar-bg: rgb(1 2 3 / 40%);
  --blur-background: rgba(2, 7, 3, 0.532);
  --shadow: rgb(255, 162, 162);
  text-align: center;
  margin: 0;

}

body .app.stone {


  width: 100%;
  font-Family: "kiena";
  display: block;
  text-align: center;
  align-items: center;
  align-self: center;
  font-family: "Lato", sans-serif;
  visibility: visible;
  font-family: Arial;
  margin: 0px;
  background-image: linear-gradient(to bottom right, #521314, #0e0000, #0e0000, #0f0000, #521314);
  animation: gradient 10s infinite;
  background-size: 110% 100%;

}

.app.stone .app {
  display: flex;
  align-items: center;
  justify-content: center;
}

.app.stone #main-page {
  font-Family: "kiena";
  font-weight: 900;
  text-align: center;

  display: flex;
  height: 100vh;
  max-height: 100%;

  justify-content: center;
  align-items: center;

}

.app.stone #main {
  width: max-content;
  max-width: 95%;
  min-width: 60%;
  max-height: 99%;
  display: flex;
}


.app.stone .apps {
  color: var(--theme-color);
  min-width: 98%;
  max-width: 98%;
  ;
  border-radius: 30px;
  opacity: 1;
  height: 95%;
  display: block;
  flex-direction: column;
  text-align: center;
  flex: 1;
  z-index: 1;
  position: relative;
  overflow: auto;

  backdrop-filter: blur(20px) brightness(0.8) saturate(100%);
  /* Эффект тени */
  transition: 0.8s ease-in-out, height 0.5s ease-out;

  border: 2px solid var(--theme-color);

}


@keyframes shadow-border-animation {
  0% {
    border-color: #8b0313;
    box-shadow: -1px 1px 10px 1px rgb(44, 1, 1);
  }

  25% {
    box-shadow: -1px 1px 10px 1px rgb(80, 7, 7);
  }

  50% {
    border-color: var(--theme-color);
    box-shadow: -1px 1px 10px 1px rgb(114, 13, 13);
  }

  75% {
    box-shadow: -1px 1px 10px 1px rgb(80, 7, 7);
  }

  100% {
    border-color: #8b0313;
    box-shadow: -1px 1px 10px 1px rgb(44, 1, 1);
  }
}





#loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
  animation: fade-out 1s forwards;
}

#loader {
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: rotate 1s linear infinite;
}

.circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #49a7e5;
  -moz-box-shadow: 0 0 10px #a0ecfe;
  -webkit-box-shadow: 0 0 10px #5eeaff;
  box-shadow: 0 0 10px #6cfaff;


  margin: 0 10px;
  transform-origin: center;
  animation: bounce 0.8s ease-in-out infinite alternate;
}

.circle:nth-child(2) {
  animation-delay: 0.1s;
}

.circle:nth-child(3) {
  animation-delay: 0.2s;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0) scaleY(1);
  }

  25% {
    transform: translateY(-20px) scaleY(2);
  }

  50% {
    transform: translateY(0) scaleY(1);
  }

  75% {
    transform: translateY(20px) scaleY(0.5);
  }

  100% {
    transform: translateY(0) scaleY(1);
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }

  99% {
    opacity: 0;
    display: none;
    background-color: none;
    color: none;
    transform: scale(1);
  }

  100% {
    background-color: none;
    color: none;
    transform: scale(0);

    opacity: 0;
    display: none;

  }
}

.hidden {
  display: none;
}








.app.stone .ct {
  display: flex;
  align-items: center;
  justify-content: center;
}


.app.stone .header {
  border-bottom: 0px solid var(--border-color);
  align-items: center;
  padding: 0px 10px;
  margin-left: 2%;
  margin-right: 2%;
  margin-top: 15px;
  margin-bottom: 1%;
  max-height: max-content;
  display: flex;
  border-radius: 14px;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 3px solid var(--border-color);
  animation: border-animation 4s infinite;
}


@keyframes body {
  0% {
    visibility: hidden;
  }

  100% {
    visibility: hidden;
    ;
  }
}



.app.stone .cont {
  display: flex;
  flex-wrap: wrap;
  max-width: max-content;
  text-align: center;
  align-items: center;
  align-self: center;

  overflow: scroll;
}

.app.stone.cont iframe {
  width: 50%;
  height: 400px;
}


.app.stone .main-header-link-fl-tb {
  margin: 0px 20px;
}

.app.stone .header-tab-show {
  display: flex;
  /* background-color: #682121; */

  margin-left: auto;
}


.app.stone .header span {
  cursor: pointer;
}

.app.stone .header-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
  transition: all 0.5s ease;

}

.app.stone .main-header-link>div:not(:last-child) {
  transition: all 0.5s ease-in-out;
}

/* Анімація кожного другого дочірнього елемента */
.app.stone .main-header-link>div:nth-child(even) {
  transition: all 0.5s ease-in-out;
}





.app.stone .lnlk {
  min-width: max-content;
}


.app.stone a:hover {
  cursor: pointer;

}




.app.stone .wrapper {
  width: 0px;
  height: 0px;
  display: flex;
}

.app.stone .left-side {
  flex-basis: 0;
  border-right: 0px solid var(--border-color);
  animation: border-animation 4s infinite;

  padding: 0;
  flex-shrink: 0;
}

.app.stone .main-header {
  display: flex;

  margin-right: auto;
  transition: all 1s;
  align-items: center;
  border-bottom: 0px solid var(--border-color);
  height: auto;


}

.app.stone .logo {
  display: block;
}

.app.stone .main-header-link a {
  min-width: 330px;

}

.app.stone.main-header-link {


  display: block;
  padding: 1px 10px;
  transition: 1s;


}


.app.stone .main-container {
  display: flex;
  flex-direction: column;
}

.app.stone .menu-link-main {
  text-decoration: none;
  color: var(--theme-color);
  padding: 0 30px;
}


::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-bg);
  border-radius: 10px;
}


.app.stone .videolist {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
}


@keyframes gradient {
  0% {
    background-position: 100% 100%;
  }

  25% {
    background-position: 70% 100%;
  }

  50% {
    background-position: 100% 100%;
  }

  75% {
    background-position: 70% 100%;
  }

  100% {
    background-position: 100% 100%;
  }
}

/* Определяем классы для анимации */
.app.stone .slide-in {
  animation: slide-in 0.5s ease-out forwards;
}

.slide-out {
  animation: slide-out 0.5s ease-out forwards;
}

@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slide-out {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}



h2 .app.stone {
  height: 20px;
  margin: 0;
  margin-top: 5px;
}

.app.stone .closebtn {
  font-size: 50pt;
  line-height: 50px;
}

.app.stone .sidenav {
  width: 400px;
  max-width: 0px;
  color: white;
  opacity: 0;
  height: 500px;
  display: flex;
  flex-direction: column;
  text-align: center;
  flex: 1;
  z-index: 1;
  position: relative;
  border-radius: 30px;
  transition: 0.7s;
  /*animation: shadow-border-animation 4s infinite;*/
  overflow: auto;

  backdrop-filter: blur(20px);
  backdrop-filter: blur(10px) brightness(0.7) saturate(120%);
  box-shadow: -1px 1px 37px 22px rgba(42, 28, 28, 0.66);
  -webkit-box-shadow: -1px 1px 37px 22px rgba(42, 28, 28, 0.66);
  -moz-box-shadow: -1px 1px 37px 22px rgba(42, 28, 28, 0.66);
  /* Эффект тени */
  border: 2px solid  var(--theme-color);

}

.app.stone .sidenav a {
  text-decoration: none;
  color: var(--theme-color);
  display: block;
  font-family: Georgia, 'Times New Roman', Times, serif;
  transition: 0.5s;
  text-decoration: none;
}

.app.stone .sidenav a:hover {
  color: rgb(255, 0, 0);
  transform: scale(1.1);
}

.app.stone #main {
  height: 100%;
  transition: margin-left 0.5s;

  transition: margin-left 0.5s ease-out;

}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
}

.app.stone #mainmenu {
  background-color: black;
}

ul .app.stone {
  padding-left: 0;
  margin-left: 0;
}

li .app.stone {
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
  cursor: pointer;
}


.app.stone .accordion {
  flex: 100%;
  color: white;
  display: block;
  margin-top: 5px;
  border: 3px solid var(--border-color);
  animation: border-animation 4s infinite;

  padding: 5px;
  border-radius: 20px;
  transition: all 0.5s ease-in-out;
}

.app.stone #search-box {
  width: 5%;
}

.app.stone#reset-button {
  margin-left: 1px;
  background-color: #f2f2f2;
  border: none;
  cursor: pointer;
}

.app.stone #search-input {
  font-weight: 0;
  color: #e6385e;
  background-color: #2b303b;
  width: 70%;
  margin-left: 15px;
  margin-bottom: 20px;
  height: 50px;
  border: 3px solid #fff;
  border-radius: 100px;
  cursor: default;
  transition: 0.5s;
  outline: none;
  display: none;

}

.app.stone #search-input:hover {
  background-color: #4f5b66;
  width: 75%;
  color: #e6385e;
  height: 55px;
  border: 3px solid #fff;
  border-radius: 20px;
  margin-bottom: 20px;
}


.app.stone.accordion-item.active .accordion-title {
  background-color: #f7f7f7;
}



.app.stone .braa {
  display: flex;
  margin: 0 0.2rem;
  align-items: center;
  justify-content: center;
  -webkit-transition-duration: 0.4s;
  transform: scale(1.1);
}


.app.stone .men-button {
  transform: scale(0.97);
  -webkit-transition-duration: 0.4s;
  max-width: 100%;
  min-width: 80%;
}


.app.stone .men-button button {
  background-color: rgb(43, 43, 43);
  border-radius: 10px;
  margin: 1px 0;
  transition-duration: 0.7s;
  font-size: 0.9em;
  padding: 6px 30px;
  color: rgb(225, 23, 23);
  border: none;
}


.app.stone .men-button button:hover {
  background-color: rgb(62, 62, 62);
  transform: scale(1.1);
  border-radius: 10px;
  margin: 1px 0;
  transition-duration: 0.7s;
  font-size: 0.9em;
  padding: 6px 30px;
  color: rgb(252, 8, 8);
  border: none;
}




.cube-container {
  flex: 100%;
  font-size: 1.5rem;
  color: white;
  width: 200px;
  height: 200px;
  margin: 100px;
  transition: all 3s;
  
 /* perspective: 800px*/;
}
.cube-container:hover{
  transform: scale(1.4);
}

.cube {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transform: rotateX(45deg) rotateY(45deg);
  animation: rotate 20s infinite linear;
}





.face {
  
  width: 200px;
  height: 200px;
  position: absolute;
  border: 2px solid #000;
  background-color: rgb(48, 48, 48);
  transition: transform 0.3s ease;
  transition: background-color .9s;
}
.face:hover{
  background-color: rgb(98, 98, 98);
}

.front {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateZ(100px);
}

.back {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateY(180deg) translateZ(100px);
}

.right {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateY(90deg) translateZ(100px);
}

.left {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateY(-90deg) translateZ(100px);
}

.top {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateX(90deg) translateZ(100px);
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateX(-90deg) translateZ(100px);
}



@keyframes rotate {
  0% {
    transform: rotateX(45deg) rotateY(45deg);
  }
  100% {
    transform: rotateX(45deg) rotateY(405deg);
  }
}
.maain-container{
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tabs{
  display: flex;
  align-items: center;
  justify-content: center;
}
