@font-face {
  font-family: "kiena";
  src: url("./kiena.ttf") format("truetype");
}
*{
  margin: 0;
  padding: 0;
}
.current-weather-container h3{
  margin: 0;
}

.weather-widget {
  margin:5px;
  font-Family: kiena;
  color:rgb(255, 47, 0);
  max-width: 75%;
  align-items: center;
  justify-content: center;
text-align:center;
  background-color: rgba(51, 51, 51, 0.75);
padding: 30px;
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;

  grid-template-columns: repeat(5, 1fr 2fr);}

.weather-search {
  font-Family: "kiena";
  max-width:60%;
  display: flex;
  flex-wrap:wrap;
  justify-content: center;
  align-items: center;
}
.weather-search input {
  max-width: 400px;
}

/* Change the width of .hourly-forecast-container to 100% */
.hourly-forecast-container {
  align-items: center;
  justify-content: center;
  display:flex;
  flex-wrap:wrap;

}

.weather-search label {
  margin-right: 10px;
  opacity: 0.8;
}

.weather-search input {
  opacity: 0.8;
}

.weather-search input:focus {
  box-shadow: inset 0 0 3px rgba(255, 255, 255, 0.5);
}

.weather-search input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.weather-description {
  font-size: 18pt;
  text-align: center;
  opacity: 0.8;
}

.weather-temp {
  font-size: 14pt;
  text-align: center;
  opacity: 0.8;
}

.weather-temp::after {
  content: "C";
  font-size: 14pt;
  margin-left: 10px;
  opacity: 0.8;
}

.weather-info {
  text-align: center;
  align-items: center;
  justify-content: center;
  min-width:99%;
  display: flex;
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 20px;
  text-align: center;
  line-height: 1.5;
  opacity: 0.8;
}

.weather-sun-info {
  border-radius: 20px;
  font-size: 14px;
  background-color: rgba(36, 36, 36, 0.862);
  color: orangered;
  padding: 5px;
  margin: 0 5px;
  text-align: center;
  line-height: 1.5;
  opacity: 0.8;
}

.weather-grid {
  max-height: 400px;
  overflow-y: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  grid-gap: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 10px 20px;
  border-radius: 20px;
  opacity: 0.8;
}

.weather-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 10px;
  opacity: 1;
}

.weather-item__key {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
}

.weather-item__value {
  font-size: 20px;
  font-weight: bold;
}

.tr {
  margin: 10px;
  display: inline-block;
  min-width: max-content;
  background-color: rgba(36, 36, 36, 0.862);
  padding: 5px;
  border-radius: 10px;
  max-width: 300px;
  height: fit-content;

}

.tr:last-child {
  margin-right: 0;
}
.weather-icon{
  font-size: 7em;
}