/* Определяем классы для анимации */
.app.sttwo .slide-in {
  animation: slide-in 0.5s ease-out forwards;
}

.app.sttwo .slide-out {
  animation: slide-out 0.5s ease-out forwards;
}

@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slide-out {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}


