.telegram-container{
  background-color: rgba(8, 22, 8, 0.012);
  color: #e6e6e6;
  padding: 0px;
  display: flex;
flex-wrap: wrap-reverse;
flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
}

.telegram-message{
  flex:50%;
  margin-bottom: 20px;
  border-radius: 15px;
margin: 1%;
  background-color: rgba(8, 210, 8, 0.111);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 30%;
  max-width: max-content;
  width: 20%;
  opacity: 0;
  animation-name: fadeIn;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  transition: all 0.3s ease-in-out;
  animation-delay: 0.1s;
}

.telegram-message:hover{
  background-color: rgba(127, 0, 0, 0.089);
  box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.3);
}

.telegram-message:nth-child(even){
  transform: translateX(10px);
}

.telegram-message:nth-child(odd){
  transform: translateX(-10px);
}

.telegram-message:first-child{
  transform: none;
}

@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}

.telegram-sender{
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}

.telegram-message-text{
  
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}

.telegram-message-details{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 12px;
  padding: 8px 0;
  width: 100%;
}

.telegram-message-details span{
  margin-right: 5px;
}

.telegram-message-details span:last-child{
  margin-right: 0;
  font-weight: bold;
}

.telegram-message img,
.telegram-message video,
.telegram-message audio{
  max-width: 90%;
  height: auto;
  margin-top: 10px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

/* styles for specific media types */
.telegram-message audio{
  display: block;
}

.telegram-message video{
  display: block;
  margin: 0 auto;
}

.telegram-message video[circle]{
  border-radius: 15%;
  width: 100px;
  height: 100px;
}

.telegram-message p:last-child{
  margin-bottom: 0;
}