:root {
  --back_color: rgb(78, 77, 77);
}


* {
  text-align: center;
  color: var(--color-font);
}

.closebtn {
  color: rgb(0, 248, 248);
}



.tab-header {
  text-align: left;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 9px 100px;
  font-weight: bold;
  cursor: pointer;
}

.tab-header:hover {
  color: rgb(0, 248, 248);
}


.taab-content {
  padding: 10px;
}

.taab-content.active {
  display: block;
}












.menu {
  border-radius: 15px;
  height: max-content;
  font-size: 0;
  border: none;
  display: none;
  ;
  transition: height 0.5s ease-in-out;
  transition: width 1.5s ease-in-out;
}

.menu.open {
  height: max-content;
  transition: height 1s ease-in-out;
  max-height: auto;
  display: block;
  font-size: 1.04em;
}

.tabs {
  margin-bottom: 0px;
}

.tab.active {
  border: none;
}

.men-button:hover .search {
  font-size: 100pt;
}

li {
  list-style-type: none;
}

ul {
  padding: 0;
}


.category {
  display: flex;
  flex-wrap: wrap;
max-height: fit-content;
  margin-top: 20px;
  text-align: left;
}

.category-header:hover {
  height:auto;
  color: #ff2b2b;
}


.category:hover .category-tabs a {
  font-size: 0.9em;
  opacity: 1;
  padding: 10px 5px;
  color: #ed0b0b;
  border: none;
  transition: 0.4s ease-in-out;
  border-radius: 10px;

}

.category-tabs {
  flex: 100%;
  padding: 0px;
  text-align: center;
  display: inline-block;
}

.category-tabs a {
  font-size: 0pt;
  opacity: 1;
  padding: 0px;

  color: var(--theme-color);
  border: none;
  margin: 0px;
  transition: 0.3s ease-in-out;
  border-radius: 10px;

}





.all {
  margin: 0% 1.5% 0% 1.5%;
  text-align: center;
  border-radius: 20px;
  width: auto;

  border: solid 3px black;

}

#menu {
  border-radius: 25px;

}




.search {
  margin-bottom: 15px;
  outline: none;
  background: transparent;
}

.search {
  background-color: rgba(0, 0, 0, 0.42);
  color: #fff;
  font-size: 1em;
  text-align: left;
  width: 100%;
  border-radius: 10px;
  height: 42px;

  border: none;
}

.search:focus {
  border-color: #66263e;
  border-radius: 0;
  transition: 1s;
}

.sidenavmenu {
  min-width: 99%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.bb {
  width: auto;
  margin: 0;
  padding: 5px;
  font-size: 2em;
}

iframe {
  height: 500px;
  width: 1000px;
}